export default {
	name: "home-list-item",
	props: {
		item: Object,
		index: Number
	},
	methods: {
		clickevent() {
			console.log('1');
			switch (this.item.clicktype) {
				case "navigateTo":
					if (this.item.url) {
						let option = {url: this.item.url};
						if(this.item.auth){
							return this.User.navigate(option,this.item.NoCheck)
						}
						uni.navigateTo(option);
					}
					break;
				case "switchTab":
					if (this.item.url) {
						uni.switchTab({
							url: this.item.url
						})
					}
					break;
				case "clear":
					uni.showModal({
						title: '提示',
						content: '是否要清除缓存?',
						confirmText: '立刻清除',
						success: res => {
							if(res.confirm){
								uni.clearStorage();
								uni.showToast({title: '清除缓存成功！'});
								this.User.logout(false);
							}
						},
					});
					break;
				case "bind":
					//如果缓存中已绑定，直接返回
					if(this.User.userbind[this.item.provider]) return;
					this.bindother();
					break;
				case "noThing":
					uni.showToast({title: "功能更新中...",icon: 'none'})
					break;
			}

		},
		bindother(){
			uni.login({
				provider: this.item.provider,



				success: (res) => {
					uni.getUserInfo({
					  provider:this.item.provider,
					  success: (infoRes)=> {
						let options = Object.assign(infoRes,res);
						this.bindEvent(this.User.__formatOtherLogin(this.item.provider,options));
					  }
					});
				},
				fail: (err) => {
					uni.showToast({ title: '绑定失败',icon:"none" });
					console.log('login fail:', err);
				}
			});
		},
		async bindEvent(data){
			uni.showLoading({title: '绑定中...', mask: false});
			let [err,res] = await this.$http.post('/user/bindother',data,{
				token: true,checkToken:true
			})
			if(!this.$http.errorCheck(err,res)) return uni.hideLoading();
			//绑定成功
			uni.hideLoading();
			uni.showToast({title: '绑定成功！'});
			//修改状态
			this.User.userbind[this.item.provider] = {
				nickname: data.nickName,
			}
			uni.setStorageSync('userbind',this.User.userbind);
			this.$emit('updateuserbind');
		}
	}
}
